	import{ mapState,mapMutations} from 'vuex'
import actionlist from "@/components/actionlist.vue";
export default {
	data() {
		return {
			vi:1,
			tab:0,//行动，围观，收藏
			cardList:[],
			lookerList:[],
			maskState:0,
			nextPage:1,//当前页数
			pageSize:10,//每页条数
			nextPageTwo:'',
			total:'',
			looktotal:'',
			pushId:'',
			index:'',
		};
	},
	onShow() {	
		// this.inDada();
	},
	onLoad() {
		this.inDada();
	},
	computed: {
	           ...mapState(['hasLogin'])  
	       },  
	onShareAppMessage(res) {
		let that = this;
		that.setSaveShareInfo(res);
		return {
			title: that.cardList[res.target.id].content,
			path: '/pages/index/action/action?pushId='+ that.cardList[res.target.id].id+'&share='+uni.getStorageSync('id'),
			imageUrl:that.cardList[res.target.id].pictures?that.cardList[res.target.id].pictures:'../../static/images/icon/img/title1.png',
		}
				
	},
	methods: {
		setSaveShareInfo(res){
			this.xd_request_post(this.xdServerUrls.xd_saveShareInfo,{
				pushId:this.cardList[res.target.id].id,
				shareUserId:uni.getStorageSync('id'),
			},true
			   ).then(res => {
				   console.log(res)
				   })
		},
		creatXd(e){
			var that=this;
			var id=that.pushId;
			var i=that.index;
			if(e==1){
				uni.showModal({
				    title: '重新编辑行动',
				    content: '将删除原行动及打卡信息，在重新发布新行动',
				    success: function (res) {
				        if (res.confirm) {
				           that.xd_request_post(that.xdServerUrls.xd_delPushDataByPushId,{pushid:id},true).then(res => {
				           	if (res.resultCode == 0) {
				           		uni.showToast({
				           		    title: '删除成功，将重新编辑',
									icon:'none',
				           		    duration: 1500,
									success() {
										try {
										    uni.setStorageSync('pushData',that.cardList[i] );
										} catch (e) {
										    // error
										}
										
										uni.navigateTo({
											url:'step1'
										})
									}
				           		});
				           	}
				           })
				        } else if (res.cancel) {
				           that.maskState=0;
				        }
				    }
				});
			}else if(e==2){
				uni.showModal({
				    title: '删除行动',
				    content: '删除后无法恢复，相关打卡也会被删除',
				    success: function (res) {
				        if (res.confirm) {
				           that.xd_request_post(that.xdServerUrls.xd_delPushDataByPushId,{pushid:id},true).then(res => {
							   console.log(res)
				            	if (res.resultCode == 0) {
				            		uni.showToast({
				            		    title: '删除成功',
										icon:'none',
				            		    duration: 1500
				            		});
									that.cardList.splice(i,1);
				            	}
				            })
				        } else if (res.cancel) {
				            that.maskState=0;
				        }
				    }
				});
			}
		},
		toggleMask(type,index){
			let timer = type >=0 ? 10 : 300;
			let	state = type >=0 ? 1 : 0;
			this.maskState = 2;
			this.pushId=type;
			this.index=index
			setTimeout(()=>{
				this.maskState = state;
			}, timer)
		},
		toAction(e){
			console.log(e)
			uni.navigateTo({
				url: '../index/action/action?pushId='+e
			});
		},
		goStep(){
			uni.navigateTo({
				url: `/pages/action/step1`
			});
		},
		
		inDada(){		
			let token='';
			let id='';
			if(!this.hasLogin){
				uni.redirectTo
					({
					url: '../login/login' 
				});
				return false;
			}
			try{
				token=uni.getStorageSync('token');
				id=uni.getStorageSync('id');
			}catch(e){
				//TODO handle the exception
			}
			   this.xd_request_post(this.xdServerUrls.xd_pushByUserIdList,
			   {
			   token:token,
			   userId:id,
			   pageNum:1,
			   pageSize:10,
			   },
			   true
			   
			       ).then(res=>{
						 this.cardList=this.dataPaly(res);
						 this.nextPage=res.obj.nextPage;
						 this.total=res.obj.total;
			   	}).catch(Error=>{
			   		console.log(Error)
			   	})
				this.xd_request_post(this.xdServerUrls.xd_getLookerByUserId,
				{
				userId:uni.getStorageSync("id"),
				pageNum:1,
				pageSize:10,
				},
				true
				
				    ).then(res=>{
									  
										 this.lookerList=res.obj.list;
										 this.nextPageTwo=res.obj.nextPage;
										 this.looktotal=res.obj.total;
					}).catch(Error=>{
						console.log(Error)
					})
			},
			getReachList(){
				if(this.tab==0){
					if(this.nextPage==0){
						uni.showLoading(
						{
							title: '没有更多数据了'
						})
						setTimeout(function () {
							uni.hideLoading();
						}, 1000);
						return false
					}
					uni.showLoading(
					{
						title: '加载中..',
						mask:true
					})
					this.xd_request_post(this.xdServerUrls.xd_pushByUserIdList,
					{
						token:uni.getStorageSync('token'),
						userId:uni.getStorageSync('id'),
						pageNum:this.nextPage,
						pageSize:this.pageSize,
					},
					false
						   ).then(res=>{
							   this.nextPage=res.obj.nextPage;
							 var data=this.dataPaly(res);		
							this.cardList = this.cardList.concat(data);					
							setTimeout(function () {
								uni.hideLoading()
							}, 1000);
							// uni.hideNavigationBarLoading();//关闭加载动画
							
						})	
				 }else if(this.tab==1){
					 if(this.nextPageTwo==0){
					 	uni.showLoading(
					 	{
					 		title: '没有更多数据了'
					 	})
					 	setTimeout(function () {
					 		uni.hideLoading();
					 	}, 1000);
					 	return false
					 }
					 uni.showLoading(
					 {
					 	title: '加载中..',
					 	mask:true
					 })
					 this.xd_request_post(this.xdServerUrls.xd_pushByUserIdList,
					 {
					 	token:uni.getStorageSync('token'),
					 	pageNum:this.nextPageTwo,
					 	pageSize:10,
					 },
					 false
					 	   ).then(res=>{
					 		   this.nextPageTwo=res.obj.nextPage;
					 		this.cardList = this.lookerList.concat(res.obj.list);					
					 		setTimeout(function () {
					 			uni.hideLoading()
					 		}, 1000);	
					 	})	
				 }
				},
				dataPaly(res){
					let dataList=res.obj.list;
					var date=new Date();
					date=date.getTime();
					
					for(var i=0;i <dataList.length;i++){
						var num=dataList[i].targetDay-dataList[i].pushCardCount;
						var num2=dataList[i].targetDay;
						var num3=dataList[i].targetDay+dataList[i].holidayDay
						var num4=dataList[i].pushCardCount;
						let d = new Date(dataList[i].createTime);
						let newD = new Date(d.setDate(d.getDate() + num3));
						newD=newD.getTime()
						let dd=Math.round((date-newD) / (1000 * 60 * 60 * 24));
						if(num>0 && dd<=0 ){
							dataList[i].btn=0//立即打卡
						}else if(num2>num4 && dd>0){
							dataList[i].btn=1}//未达成
							else if(num==0&&num2==num4){
								dataList[i].btn=2}	//已完成    
						// var  time=this.xdUniUtils.xd_timestampToTime(res.obj.list[i].createTime);
						// dataList[i].createTime=time;
						dataList[i].challengeRmb=Math.floor(dataList[i].challengeRmb/100);		
						
					}
					
					return dataList;
				}
				
	},
	onReachBottom() {
		this.getReachList()
	},
	onPullDownRefresh() {
		this.inDada();
	},
	components:{
		actionlist
	}
};
